import React from "react";
import "./MainButton.css";
import 'bootstrap-icons/font/bootstrap-icons.css';

const MainButton = ({ title, icon, onClick, isLightColor = false }) => {
    return (
      <button className={`main-button-container ${isLightColor ? 'light' : ''}`} onClick={onClick}>
        <div className={`main-button ${isLightColor ? 'light' : ''}`}> {title} 
        <span className="main-button-icon">{icon}</span>
        </div>
      </button>
    );
};

export default MainButton;
