import React from "react";
import SectionBox from "../../../components/Section/SectionBox";
import SkillsCollection from "../../../components/Skills/SkillsCollection/SkillsCollection";
import "./MySkills.css";

// This component displays the user's skills and preferences
const MySkills = ({ user }) => {

  console.log("User in MySkills:", user);

  // If user is not available or has no skills/preferences, show a fallback message
  if (!user || (!user.skills && !user.preferences)) {
    return <p>No skills or preferences found.</p>;
  }

  // Header title
  const title = (
    <div className="skills-header">
      <span className="skills-title">Skills and preferences</span>
    </div>
  );

  return (
    <SectionBox
      title={title}
      content={
        <div className="skills-container">
          {/* Display skills section if user has skills */}
          {user.skills && user.skills.length > 0 && (
            <div className="skills-section">
              <h4>Skills</h4>
              <SkillsCollection skills={user.skills} user={user} />
            </div>
          )}

          {/* Display preferences section if user has preferences */}
          {user.preferences && user.preferences.length > 0 && (
            <div className="preferences-section">
              <h4>Preferences</h4>
              <SkillsCollection skills={user.preferences} user={user} />
            </div>
          )}
        </div>
      }
      // Styling the section box container
      style={{ minHeight: "240px", width: "80%", border: "none" }}
    />
  );
};

export default MySkills;
