import React from 'react';
import {
  PieChart, Pie, Cell,
  BarChart, Bar, XAxis, YAxis, Tooltip, Legend,
  ResponsiveContainer
} from 'recharts';
import './ManagerProjectStats.css';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

const ManagerProjectStats = ({ project }) => {
  console.log("Project data", project);

  if (!project || !project.tasks || !project.team) {
    return <div>No project data available...</div>;
  }

  // Prepare data for charts
  const taskStatusData = [
    { name: 'To Do', value: project.tasks.filter(task => task.status === 'To Do').length },
    { name: 'In Progress', value: project.tasks.filter(task => task.status === 'In Progress').length },
    { name: 'Done', value: project.tasks.filter(task => task.status === 'Done').length },
  ];

  const taskAssignmentData = project.team.map(member => ({
    name: member.username,
    value: project.tasks.filter(task => task.assignedTo && task.assignedTo._id === member._id).length,
  }));

  console.log("Task assignment data", taskAssignmentData);

  return (
    <div className="project-stats-container">
      <div className="project-stats-container-top">
      
      {/* Project Progress Gauge */}
      <div className="project-stats-section">
        <h4 className="project-stats-title">Project Progress</h4>
        <ResponsiveContainer width={300} height={300}>
          <PieChart>
            <Pie
              data={[{ name: 'Progress', value: project.progress }, { name: 'Remaining', value: 100 - project.progress }]}
              cx="50%" cy="50%" innerRadius={60} outerRadius={80} dataKey="value" nameKey="name"
            >
              <Cell key="progress" fill="#00C49F" />
              <Cell key="remaining" fill="#FFBB28" />
            </Pie>
            <Legend />
          </PieChart>
        </ResponsiveContainer>
      </div>

      {/* Task Status Breakdown */}
      <div className="project-stats-section">
        <h4 className="project-stats-title">Task Status Breakdown</h4>
        <ResponsiveContainer height={300}>
          <PieChart>
            <Pie
              data={taskStatusData}
              cx="50%" cy="50%" outerRadius={80} fill="#8884d8" dataKey="value" nameKey="name" label
            >
              {taskStatusData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
              ))}
            </Pie>
            <Legend />
          </PieChart>
        </ResponsiveContainer>
      </div>
      </div>

      <div className="project-stats-container-bottom">
        {/* Task Assignment by User */}
        <div className="project-stats-section">
          <h4 className="project-stats-title">Task Assignment by User</h4>
          <ResponsiveContainer width={300} height={300}>
            <BarChart data={taskAssignmentData}>
              <XAxis dataKey="name" />
              <YAxis domain={[0, 'dataMax']} allowDecimals={false} />
              <Tooltip />
              <Bar dataKey="value" fill="#82ca9d" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
};

export default ManagerProjectStats;
