import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import './ProfileScreen.css';
import ProfilePic from '../../../components/ProfilePics/ProfilePic/ProfilePic';
import Navbar from '../../../components/Navbar/Navbar';
import { jwtDecode } from 'jwt-decode';
import 'bootstrap-icons/font/bootstrap-icons.css';

const ProfileScreen = () => {
  const navigate = useNavigate();
  const { username, domain } = useParams();
  const [user, setUser] = useState(null);
  const [loggedInUser, setLoggedInUser] = useState(null);
  const [isOwner, setIsOwner] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [editedName, setEditedName] = useState({ firstName: '', lastName: '' });
  const [isLoading, setIsLoading] = useState(true);
  const [previewProfilePic, setPreviewProfilePic] = useState(null);


  // Fetch the user whose profile is displayed
  useEffect(() => {
    const token = localStorage.getItem('token');

    const fetchUser = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        if (!res.ok) throw new Error('Failed to fetch user');
        const data = await res.json();
        setUser(data);
      } catch (error) {
        console.error('Error fetching user:', error.message);
      } finally {
        setIsLoading(false);
      }
    };

    const fetchLoggedInUser = async (loggedUsername) => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${loggedUsername}`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        const data = await res.json();
        setLoggedInUser(data);
      } catch (err) {
        console.error('Error fetching logged-in user:', err);
      }
    };

    // Decode token to determine if logged-in user is profile owner
    if (token) {
      try {
        const decoded = jwtDecode(token);
        setIsOwner(decoded.username === username);
        fetchLoggedInUser(decoded.username);
      } catch (err) {
        console.error('Invalid token:', err);
      }
    }
    fetchUser();
  }, [username, domain]);

  // Handle forgot password
  const handleForgotPassword = () => {
    navigate('/forgot-password');
  };

    // Handle edit button click (pencil icon)
  const handleEditClick = () => {
    setEditedName({ firstName: user.firstName, lastName: user.lastName });
    setPreviewProfilePic(null);
    setIsEditing(true);
  };

  // Handle name input changes
  const handleNameChange = (e) => {
    const { name, value } = e.target;
    setEditedName((prev) => ({ ...prev, [name]: value }));
  };

  // Handle image selection and preview
  const handleImageChange = (e) => {
    const file = e.target.files[0];

    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setPreviewProfilePic(reader.result); // Base64 string
      };
      reader.readAsDataURL(file);
    }
  };

  // Handle save name + profile picture together
  const handleSaveUpdate = async () => {
    const token = localStorage.getItem('token');
    if (!token) return;

    const updatedFields = {
      firstName: editedName.firstName,
      lastName: editedName.lastName,
    };

    if (previewProfilePic) {
      updatedFields.profilePic = previewProfilePic;
    }

    try {
      const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(updatedFields),
      });

      if (!res.ok) throw new Error('Failed to update user');

      const updatedUser = await res.json();
      setUser(updatedUser);
      setIsEditing(false);
      setPreviewProfilePic(null);
    } catch (error) {
      console.error('Error updating user:', error.message);
    }
  };

  if (isLoading) {
    return (
      <div className="loading-screen">
        <div className="spinner" />
      </div>
    );
  }

  if (!user) return <div>Loading...</div>;

  return (
   <>
      <Navbar user={loggedInUser} domain={domain} />
      <div className="profile-container">
        <div className="profile-card">
          <div className="profile-pic-wrapper">
            <ProfilePic picture={previewProfilePic || user.profilePic} size={100} iconColor="#888" />
            {isOwner && user.role === 'organization_head' && isEditing && (
              <>
                <input type="file" accept="image/*" onChange={handleImageChange} />
              </>
            )}
          </div>

          <h2>
            {isEditing ? (
              <>
                <input
                  type="text"
                  name="firstName"
                  value={editedName.firstName}
                  onChange={handleNameChange}
                  className="edit-name-input styled-input"
                />
                <input
                  type="text"
                  name="lastName"
                  value={editedName.lastName}
                  onChange={handleNameChange}
                  className="edit-name-input styled-input"
                />
                <button onClick={handleSaveUpdate} className="save-name-btn styled-button">Save</button>
              </>
            ) : (
              <>
                {user.firstName} {user.lastName}
                {isOwner && user.role === 'organization_head' && (
                  <i
                    className="bi bi-pencil-fill edit-icon"
                    onClick={handleEditClick}
                    style={{
                      cursor: 'pointer',
                      marginLeft: '8px',
                      fontSize: '1rem',
                      color: '#888',
                    }}
                  ></i>
                )}
              </>
            )}
          </h2>

          <p>{user.email}</p>
          <p className="role">{user.role.replace('_', ' ')}</p>
          {isOwner && user.role === 'team_member' && (
            <p className="exp">Experience: {user.experience} years</p>
          )}
          {isOwner && (
            <button className="forgot-password-btn" onClick={handleForgotPassword}>
              Forgot Password?
            </button>
          )}
        </div>

        <div className="profile-info">
          <div className="info-row"><strong>Username:</strong> {user.username}</div>
          <div className="info-row"><strong>Email:</strong> {user.email}</div>
          <div className="info-row"><strong>Role:</strong> {user.role}</div>

          {user.role === 'team_member' && (
            <>
              <div className="info-row"><strong>Experience:</strong> {user.experience}</div>
              <div className="info-row">
                <strong>Skills:</strong> {user.skills?.map(s => s.label).join(', ') || 'None'}
              </div>
              <div className="info-row">
                <strong>Preferences:</strong> {user.preferences?.map(p => p.label).join(', ') || 'None'}
              </div>
              <div className="info-row"><strong>Projects:</strong> {user.projects.length}</div>
              <div className="info-row"><strong>Tasks:</strong> {user.tasks.length}</div>
            </>
          )}

          <div className="info-row"><strong>Notifications:</strong> {user.notifications.length}</div>
        </div>
      </div>
    </>
  );
};

export default ProfileScreen;
